/* ++++++++++++++++++++
+++ LAYOUT: LOGOS +++
++++++++++++++++++++ */

.logos {
	padding: 0 $gap * 4;
	position: relative;

	&-outer {
		&.secondary {
			.logo-carousel {
				.slick-arrow {
					&:before {
						color: $white;
					}
					&:hover {
						&:before {
							color: $black;
						}
					}
				}
				.slick-dots {
					button {
						&:before {
							color: $white;
						}
					}
				}
			}
		}
	}

	&-item {
    	padding: 1em;

		img {
			aspect-ratio: 3/2;
			object-fit: contain;
			mix-blend-mode: color-burn;
		}
	}
	&-title {
		font-weight: $light;
		padding-top: $gap / 1.5;
		text-align: center;
	}
}
