/* ++++++++++++++++++++++++
+++ LAYOUT: BREADCRUMBS +++
++++++++++++++++++++++++ */

.center {
	.breadcrumb-list {
		@include break(small) {
			text-align: center;
		}
	}
}

.breadcrumb {
	font-size: 0.9rem;
	margin-top: 0;
	padding: $gap 0;

	&-outer {
		background-color: $white;
		border-bottom: solid 1px $grey60;
		padding: $gap / 1.5 0;
	}

	&-list {
		display: block;

		&-item {
			display: none;
			margin-top: 0;

			&:after {
				display: none;
			}

			@include break(small) {
				display: inline-block;
			}

			&:has(+.current) {
				display: inline-block;
				.breadcrumb-list-link {
					padding-left: $gap * 1.3;
					padding-right: 0;

					&:after {
						left: 0;
						right: auto;
						transform: translate(0, -50%) rotate(180deg);
					}

					@include break(small) {
						padding-left: 0;
						padding-right: $gap * 1.3;

						&:after {
							left: auto;
							right: 0;
							transform: translate(0, -50%) rotate(0);
						}
					}
				}
			}

			&:last-child {
				.breadcrumb-list-link {
					&:after {
						display: none;
					}
				}
			}
		}

		&-link {
			@extend %transition;
			padding-right: $gap * 1.3;
			position: relative;
			text-decoration: underline;

			&:hover {
				color: $pink;
				text-decoration: underline;
			}

			&.current {
				padding-right: 0;
				text-decoration: none;
			}
			&:after {
				@include centerer(false, true);
				background-image: url("data:image/svg+xml, %3Csvg width='24' height='24' viewBox='0 0 24 24' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M8.707 20.707l-1.414-1.414L14.586 12 7.293 4.707l1.414-1.414L17.414 12z'/%3E%3C/svg%3E%0A");
				background-size: 100%;
				background-repeat: no-repeat;
				content: '';
				display: inline-block;
				height: 16px;
				right: 0;
				width: 16px;
			}
		}
	}
}
