/* +++++++++++++++++
+++ LAYOUT: HERO +++
+++++++++++++++++ */

body.home {
	.hero-title {
		@extend %h1;
		padding: 0;
	}
}

.hero {
	@extend %flex;
	position: relative;

	@include break(medium) {
		flex-flow: row-reverse nowrap;
	}

	&-outer {
		padding: 0;

		&.full {
			.hero-image {
				@include flexbox(100%);
				height: 100%;
				width: 100%;

				@include break(tablet) {
					left: 0;
					position: absolute;
					top: 0;
				}
				img {
					@include object-fit(2/1, 100%);
				}
				+ .hero-info {
					@include flexbox(80rem);
					margin: 0 auto;

					@include break(tablet) {
						padding: 0 $gap * 2;
					}

					.hero-inner {
						background-color: rgba($white, 0.7);
						color: $black;
						margin: 0;
						padding: $gap * 2;
						position: relative;
						text-align: center;
						width: auto;
						z-index: 2;

						@include break(tablet) {
							@include flexbox(66.66%);
							border-radius: $corner;
							box-shadow: $shadow;
							margin: $gap * 5 auto;
							padding: $gap * 2 $gap * 3 $gap * 3;
						}
					}
				}
			}
			.hero-summary {
				p {
					padding: 0;
				}
			}
			&.left-inner {
				.hero-image {
					+ .hero-info {
						.hero-inner {
							@include break(tablet) {
								@include flexbox(50%);
								margin: $gap * 5 auto $gap * 5 0;
								text-align: left;
							}
						}
					}
				}
			}
			&.right-inner {
				.hero-image {
					+ .hero-info {
						.hero-inner {
							@include break(tablet) {
								@include flexbox(50%);
								margin: $gap * 5 0 $gap * 5 auto;
								text-align: right;
							}
						}
					}
				}
			}
		}
		&.solid {
			.hero-image {
				+ .hero-info {
					background-color: transparent;
					box-shadow: none;

					@include break(tablet) {
						@include flexbox(92%);
						margin: 25em auto 0;
						padding: 0;
					}

					@include break(medium) {
						@include flexbox(80em);
						padding: 0 $gap * 2;
					}

					.hero-inner {
						box-shadow: $shadow;
						background-color: $primary;
						color: $white;
						margin: 0;
						text-align: left;

					    @include break(medium) {
						    @include flexbox(64%);

					    }
					}
				}
			}
		}
		&.listing {
			text-align: center;

			.hero-info {
				background-color: $secondary;

				.hero-inner {
					margin: 0 auto;

					@include break(medium) {
						width: $page-width / 1.5;
					}
					.hero-title, .hero-summary {
						@include break(medium) {
							width: 100%;
						}
					}
				}
			}
		}
	}
	&-info, &-image {
		margin: 0;
	}
	&-info {
		@extend %flex;
		@include flexbox(100%);
		background-color: $primary;
		align-items: center;

		.hero-summary {
			padding-top: $gap;
		}

		p {
			@include break(medium) {
				padding-right: 5rem;
			}
		}
		a {
			margin-top: 2rem;
			text-decoration: underline;
		}
		.button {
			text-decoration: none;
		}
	}
	&-inner {
		color: $white;
		margin: 0 auto;
		padding: 2rem 1.5rem;
		width: 100%;

		@include break(small) {
			padding: 3rem 2rem;
		}

		@include break(tablet) {
			padding: 4rem 3rem 4rem 2em;
			width: 80em;
		}

		.hero-title, .hero-summary {
			word-break: break-word;

			@include break(medium) {
				margin-left: 0;
				width: 66.66%;
			}
		}
	}
	&-image {
		@include flexbox(100%);
		overflow: hidden;
		position: relative;

		@include break(medium) {
			@include flexbox(50%);
		}
		+ .hero-info {
			@include break(medium) {
				@include flexbox(50%);
			}
			.hero-inner {
				margin: 0 0 0 auto;

				@include break(medium) {
					width: 40em;
				}
				.hero-title, .hero-summary {
					@include break(medium) {
						margin-left: 0;
						width: 100%;
					}
				}
			}
		}
		img {
			@include object-fit(16/9, 100%);
		}
	}
}
