/* +++++++++++++++++
+++ LAYOUT: MAIN +++
+++++++++++++++++ */

// Main is the high level container of content, in between the site header and footer

.main {
	background: $body-color;
	display: block;
	margin: 0;

	&-header,
	&-footer {
		text-align: center;
	}

	&-header {
		&-outer {
			background-color: $primary;
			color: $white;
		}
		&-title {
			padding-bottom: $gap;
		}
		&-summary {
			p {
				font-size: 1.2em;
			}
		}
	}

	&-section {
		@include break(medium) {
			flex-flow: row wrap;
		}

		&.single  {
			.wysiwyg-outer {
				&.with-sidebar {
					.container {
						flex-direction: row-reverse;

						.content {
							margin: 0 auto 0 0;
						}
					}
				}
			}
			.section {
				&.inline {
					.container, .cta, .cta.with-image {
						div, ul {
							margin: 0 auto 0 0;
						}
					}
				}
			}
		}
	}
	&-share {
		@extend %flex;
		@extend %transition;
		align-items: center;
		background-color: $secondary;
		flex-wrap: nowrap;
		justify-content: center;
		margin: 0 auto;
		padding: $gap / 1.5 $gap / 2;
		position: -webkit-sticky;
		position: sticky;
		right: 0;
		top: 0;
		width: 100%;

		@include break(xsmall) {
			flex-wrap: nowrap;
		}

		@include break(medium) {
			box-shadow: none;
			display: block;
			padding: $gap / 2 $gap / 1.5;
		}

		&-outer {
			@extend %transition;
			@include flexbox(auto);
			bottom: 0;
			display: inline-block;
			left: 50%;
			position: fixed;
			transform: translate(-50%, 0);
			z-index: 998;

			@include break(xsmall) {
				width: auto;
			}

			@include break(medium) {
				left: auto;
				position: absolute;
				right: 0;
				top: $gap * 2;
				transform: none;
			}
			&.hide-share {
				display: none;
				pointer-events: none;
				@include break(medium) {
					opacity: 0;
					pointer-events: none;
				}
			}
		}
		&-item {
			margin: 0 $gap / 5;
			height: 30px;

			@include break(tablet) {
				height: 44px;
			}
			@include break(medium) {
				margin: $gap / 2 0;
			}

			&.title {
				height: 100%;
				margin: 0;
				padding-bottom: $gap / 2;
				text-align: center;
				width: 100%;

				@include break(xsmall) {
					padding-bottom: 0;
					text-align: left;
					width: 46px;
				}

				@include break(medium) {
					padding-right: 0;
					width: auto;
				}

				h2 {
					font-size: 1.2rem;
					font-weight: $bold;
					width: 100%;

					@include break(medium) {
						font-size: 1.375rem;
						line-height: 26.4px;
						margin-left: -9px;
						text-align: center;
						width: 62px;
					}
				}
			}
		}
		&-item-link {
			@extend %transition;
			background-color: $white;
			border-radius: 100%;
			display: inline-block;
			height: 30px;
			padding: 0;
			position: relative;
			width: 30px;

			@include break(tablet) {
				height: 44px;
				width: 44px;
			}

			.icon {
				@include centerer(true, true);
				height: 100%;
				width: 100%;
			}

			&:hover {
				background-color: $black;

				.icon {
					color: $white;
				}
				&.close {
					@include close('white');
					background-position: center;
					background-repeat: no-repeat;
					background-size: 12px;
					cursor: pointer;

					@include break(tablet) {
						background-size: 16px;
					}
				}
			}
			&.print {
				cursor: pointer;
			}
			&.twitter, &.link, &.email, &.print {
				.icon {
					width: 16px;
					@include break(tablet) {
						width: 22px;
					}
				}
			}
			&.facebook, &.instagram {
				.icon {
					width: 22px;

					@include break(tablet) {
						width: 28px;
					}
				}
			}
			&.youtube {
				.icon {
					width: 24px;
				}
			}
			&.tiktok {
				.icon {
					width: 14px;
					@include break(tablet) {
						width: 18px;
					}
				}
			}
			&.linkedin {
				.icon {
					margin-top: 2px;
					width: 1.2rem;

					@include break(tablet) {
						width: 1.4rem;
					}
				}
			}
			&.close {
				@include close('black');
				background-position: center;
				background-repeat: no-repeat;
				background-size: 12px;
				cursor: pointer;

				@include break(tablet) {
					background-size: 16px;
				}
			}
			.icon {
				@extend %transition;
				@include centerer(true, true);
				color: $primary;
				margin: 0;
			}
		}
	}
}
