/* ++++++++++++++++++++
+++ LAYOUT: GALLERY +++
++++++++++++++++++++ */

.gallery {
	margin: $gap * 1.5 0;
	position: relative;

	&-item {
        margin: 0;
        text-align: center;
	}
	&-thumb {
		height: 100%;
		padding: 0 $gap;

		img {
			@include object-fit(4/3, 100%);
		}
	}
	.figcaption-icon {
		left: $gap;
	}

	&-outer {
		.section-header {
			+ .container {
				padding: 0;
				@include break(small) {
					padding: 0 $gap / 1.5;
				}
				@include break(medium) {
					padding: 0 $gap * 2;
				}
			}
		}

		&.inline {
			.gallery {
				@include break(medium) {
					margin: 0 0 0 auto;
					width: 66.66%;
				}
			}
		}
	}
}
.slick-arrow {
	display: none !important;

	@include break(small) {
		display: block !important;
	}

	&:hover {
		box-shadow: none;
	}
}
