/* ++++++++++++++++++++
+++ LAYOUT: SIDEBAR +++
++++++++++++++++++++ */

// Represents content that is tangentially related to the content nearby, such as tags and subnavigation. within the {% block page %} the sidebar is optional and if included will trigger a two column layout

.sidebar {
	@extend %transition;
	@extend %font-header;
	background-color: $primary;
	color: $white;
	margin-bottom: $gap;

	@include break(medium) {
		margin: 0 0 0 auto;
		top: 0;
	}

	&-outer {
		@include flexbox(100%);
		margin: 0;
		z-index: 2;

		@include break(medium) {
			@include flexbox(30%);
		}
	}

	&.hide-for-mob {
		display: none;
		@include break(medium) {
			display: block;
		}
	}

	&-show-button {
		@extend %transition;
		@include bg-arrow('white');
		background-color: transparent;
		border: 0;
		display: block;
		height: 30px;
		position: absolute;
		right: $gap * 1.5;
		transform: rotate(90deg);
		top: $gap * 1.3;
		width: 30px;

		&:hover {
			transform: rotate(180deg);
		}
		&.active {
			transform: rotate(270deg);
		}
		@include break(medium) {
			display: none;
		}
	}

	&-inner {
		display: none;

		@include break(medium) {
			display: block !important;
		}
	}

	&-block + &-block {
		margin-top: $gap * 3;
	}

	&-title, &-meta-title {
		background-color: $grey15;
		font-size: 1.25rem;
		font-weight: $bold;
		display: block;
		margin: 0;
		padding: $gap * 1.5;
	}
	&-meta-title {
		@include break(medium) {
			display: none;
		}
	}

	&-block {
		position: relative;
	}

	&-nav {
		padding: $gap $gap * 1.5;

		&-item {
			border-bottom: solid 1px $white;
			position: relative;

			&:last-child {
				border-bottom: 0;
			}

			.rnav-arrow {
				display: inline-block;
				float: right;
				margin-top: -$gap * 1.5;
				padding: 0;
				position: relative;

				.icon {
					fill: $black;
				}
			}

			&.current {
				padding-bottom: $gap / 1.5;
				.sidebar-nav-list {
					display: block;
				}
			}

			.subnav_element {
				display: block;
				font-weight: $normal;
				padding: $gap / 1.5 0;
				position: relative;

				&:hover {
					text-decoration: underline;
				}
				&.current {
					font-weight: $bold;
					padding-left: $gap;

					&:before {
						background-color: $tertiary;
						border-radius: 25px;
						content: '';
						display: block;
						height: 16px;
						left: 0;
						position: absolute;
						top: 12px;
						width: 2px;
					}
				}
			}

			&.current-menu-item {
				.subnav_element {
					font-weight: $bold;
				}

				.sidebar-nav-item {
					.subnav_element {
						color: inherit;
						font-weight: $normal;
					}
				}
			}
		}
		&-list {
			.sub-list {
				display: none;
				overflow: hidden;
				transition: height 500ms ease;

				& & {
					margin-left: $gap;
				}

				.rnav-arrow {
					margin-top: -$gap * 2;
				}
				&.current-nav {
					display: block;
					max-height: 500px;
				}

				.sidebar-nav-item {
					border-bottom: 0;
				}

				.sidebar-nav-item-link {
					display: block;
					font-size: 0.9em;
					font-weight: $normal;
					position: relative;

					&:before {
						background-image: url("data:image/svg+xml,%3Csvg width='8' height='14' viewBox='0 0 8 14' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 13L7 7L1 1' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E%0A");
						content: '';
						display: block;
						height: 12px;
						left: -14px;
						position: absolute;
						top: 2px;
						width: 7px;
					}

					&:hover {
						text-decoration: underline;
					}

					&.current {
						font-weight: $bold;
					}
				}

				.sidebar-nav-item {
					padding: $gap / 2 0 $gap / 2 $gap;

					&.current-menu-item {
						font-weight: $bold;
					}
				}

				.sub-list {
					padding-left: $gap;
					.sidebar-nav-item-link {
						font-size: 0.8em;
					}
				}
			}
		}
	}

	&-meta {
		@extend %flex;
		@extend %font-content;
		align-items: flex-start;
		justify-content: space-between;
		margin: 0;
		position: relative;

		&-block {
			&:last-child {
				border-radius: 0 0 15px 0;
			}

			&-inner {
				display: block;
				width: 100%;
			}

			.press-mail {
				a {
					.icon-outer {
						background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 -3.05176e-05H1C0.734867 0.000262449 0.4807 0.105728 0.293233 0.293203C0.105767 0.480678 0.0003 0.734836 0 0.999969V4.99997C0.000292967 5.2651 0.105758 5.51927 0.293233 5.70674C0.480708 5.8942 0.734867 5.99967 1 5.99997H7C7.26513 5.99968 7.5193 5.89421 7.70677 5.70674C7.89423 5.51926 7.9997 5.2651 8 4.99997V0.999969C7.99971 0.734836 7.89424 0.480669 7.70677 0.293203C7.51929 0.105736 7.26513 0.000269482 7 -3.05176e-05ZM1 0.499969H7C7.13255 0.5001 7.25967 0.552834 7.35342 0.646553C7.44713 0.740303 7.49987 0.867419 7.5 0.999969V1.12314L4 3.68989L0.5 1.12314V0.999969C0.50013 0.867419 0.552865 0.740303 0.646583 0.646553C0.740333 0.552836 0.86745 0.500103 1 0.499969ZM7 5.49997H1C0.86745 5.49984 0.740333 5.4471 0.646583 5.35339C0.552867 5.25964 0.500133 5.13252 0.5 4.99997V1.74347L3.85208 4.20164C3.89502 4.23305 3.94684 4.24998 4.00003 4.24998C4.05323 4.24998 4.10505 4.23305 4.14798 4.20164L7.50007 1.74347V4.99997C7.49994 5.13252 7.4472 5.25964 7.35348 5.35339C7.25973 5.4471 7.13255 5.49984 7 5.49997Z' fill='black'/%3E%3C/svg%3E%0A");
						background-repeat: no-repeat;
						background-position: center;
						background-size: 12px;
					}
				}
			}
			.press-tel {
				a {
					.icon-outer {
						background-image: url("data:image/svg+xml,%3Csvg width='6' height='9' viewBox='0 0 6 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M5 0C5.54834 0 6 0.469034 6 1.03846V1.73077C6 1.92079 5.84965 2.07692 5.66667 2.07692H0.666667V6.92308H4.33333H4.33799C4.52097 6.92308 4.67132 7.07921 4.67132 7.26923C4.67132 7.45926 4.52097 7.61538 4.33799 7.61538H4.33333H0.666667V7.96154C0.666667 8.05327 0.701667 8.14118 0.764338 8.20626C0.827009 8.27135 0.911662 8.30769 1 8.30769H5C5.18834 8.30769 5.33333 8.15712 5.33333 7.96154V3.11538V3.11055C5.33333 2.92053 5.48368 2.7644 5.66667 2.7644C5.84965 2.7644 6 2.92053 6 3.11055V3.11538V7.96154C6 8.53097 5.54834 9 5 9H1C0.735004 9 0.480347 8.89027 0.292987 8.69574C0.105662 8.50121 0 8.23673 0 7.96154V1.03846C0 0.763274 0.105662 0.498822 0.292987 0.304255C0.480311 0.109726 0.735004 0 1 0H5ZM5.33333 1.38462V1.03846C5.33333 0.84288 5.18834 0.692308 5 0.692308H1C0.911667 0.692308 0.827013 0.728654 0.764338 0.793735C0.701662 0.858817 0.666667 0.946726 0.666667 1.03846V1.38462H5.33333Z' fill='black'/%3E%3C/svg%3E%0A");
						background-repeat: no-repeat;
						background-position: center;
						background-size: 8px;
					}
				}
			}

			&.info {
				@include flexbox(100%);

				.sidebar-meta-block {
					&.date, &.author, &.startdate, &.time, &.time, &.location, &.price, &.sidebar-button {
						padding: 0 $gap $gap;
					}
				}
			}
			&:first-child {
				margin-top: 0;
			}
			&:last-child {
				margin-bottom: 0;
			}
			&.tags {
				background-color: $grey20;
				margin: 0;
				padding: $gap * 1.5;
			}
			&.image {
				@include flexbox(100%);
				padding: $gap;

				@include break(small) {
					@include flexbox(48%);
				}
				@include break(medium) {
					@include flexbox(100%);
				}

				img {
					@include object-fit(4/5, 100%);
				}
			}

			.smart-date {
				&--time, &--date {
				  font-weight: 500;
				}
				&--time:after, &--date:before {
				  content: ' ';
				  display: block;
				}
			}
			&.press {
				padding: $gap;

				p {
					padding-top: $gap / 2;

					a {
						padding-left: $gap * 2;
						position: relative;

						.icon-outer {
							@extend %transition;
							@include centerer(false, true);
							background-color: $white;
							border-radius: 100%;
							height: 20px;
							left: 0;
							width: 20px;

							.icon {
								@include centerer(true, true);
								color: $primary;
								width: 12px;
							}
						}
					}
				}
			}

			.meta-show-button {
				@extend %transition;
				background-color: $white;
				border-radius: 25px 25px 0 0 ;
				cursor: pointer;
				display: block;
				margin: 0 auto;
				padding: $gap $gap * 3.5 $gap $gap * 1.3;
				position: relative;

				@include break(tablet) {
					display: none;
				}

				&:after {
					@extend %transition;
					@include centerer(false, true);
					@include bg-arrow('black');
					background-repeat: no-repeat;
					background-size: 100%;
					content: '';
					display: inline-block;
					height: 25px;
					right: 18px;
					transform: translate(0, -50%) rotate(90deg);
					width: 25px;
				}

				span {
					@extend %font-header;
					color: $black;
				}
				&:hover, &.active, &:focus {
					background-color: $white;
					text-decoration: none;

					&:after {
						@include bg-arrow('black');
						transform: translate(0, -50%) rotate(180deg);
					}
				}
				&.active {
					background-color: #00788A;

					span {
						color: $white;
					}

					&:after {
						@include bg-arrow('white');
						transform: translate(0, -50%) rotate(270deg);
					}
				}
			}
			.meta-inner {
				display: none;

				@include break(tablet) {
					display: block !important;
				}
			}

			&-title {
				@extend %font-header;
				font-size: 1rem;
				color: $white;
				font-weight: $bold;
				margin: 0 0 $gap / 2;
				width: 100%;
			}

			&-cat {
				display: inline-block;
				font-size: 0.875rem;
				font-weight: $bold;
				margin: $gap / 4 $gap / 4 $gap / 2 0;
				text-decoration: underline;

				&:hover {
					color: $black;
				}
			}

			&-tag {
				@extend %transition;
				border: solid 1px $white;
				border-radius: 25px;
				color: $white;
				display: inline-block;
				font: $normal 0.875rem/1.5 $font-header;
				margin: $gap / 2 $gap / 4 $gap / 4 0;
				padding: $gap / 5 $gap $gap / 6;

				&:hover {
					background: $black;
					border-color: $black;
					color: $white;
				}
			}
			&.share {
				margin: 0;
				padding: $gap;
				width: 100%;
			}
			&.downloads {
				background-color: $white;
				color: $black;
				padding: $gap;
				width: 100%;

				.icon {
					display: inline-block;
					margin: 0 $gap / 3 0 0;
					width: 20px;
				}

				.sidebar-meta-block-title {
					color: $primary;
				}
				.downloads-link {
					@extend %transition;
					display: block;
					font-size: 0.9rem;
					padding: $gap / 2 0 0 $gap * 2;
					position: relative;

					&:hover {
						color: $primary;
					}

					.icon {
						left: 0;
						position: absolute;
						top: 4px;
					}
				}
			}
		}
		&-share {
			@extend %flex;
			flex-wrap: nowrap;
			gap: 0 $gap;
			width: 100%;

			&-title {
				@extend %font-content;
				color: $white;
				font-weight: $bold;
				width: 100%;
			}

			&-button {
				@extend %flex;
				@extend %transition;
				align-items: center;
				border-radius: $corner;
				flex-grow: 1;
				background-color: $white;
				justify-content: center;
				padding: $gap / 2 0;
				position: relative;

				&:hover {
					background-color: $secondary;
				}
				&.instagram, &.email {
					.icon {
						height: 1.4rem;
						width: 1.4rem;
					}
				}
				&.twitter, &.whatsapp, &.link {
					.icon {
						height: 1rem;
						width: 1rem;
					}
				}
				.icon {
					color: $black;
				}
			}
		}
	}
	&-accordion {
		@extend %font-header;
		&-outer {
			position: relative;
		}

		&-title {
			@extend %h6;
			color: $secondary;
			padding: $gap $gap 0;
		}

		&-item {
			@extend %flex;
			position: relative;

			& + & {
				&:before {
					@include centerer(true, false);
					background-color: $white;
					border-radius: 25px;
					content: '';
					display: block;
					height: 2px;
					top: 0;
					width: 100%;

					@include break(medium) {
						width: 90%;
					}
				}
			}
		}

		&-trigger {
			* {
				pointer-events: none;
			}
			&.active {
				+ .sidebar-accordion-image {
					@include flexbox(30%);
					background-color: #EDECEC;
					opacity: 1;
					// padding: $gap / 1.5 0 $gap / 1.5 $gap / 1.5;

					+ .sidebar-accordion-info {
						@include flexbox(70%);
						background-color: #EDECEC;
						font-size: 1.125rem;
					}
				}
			}
		}

		&-button {
			@extend %transition;
			background-color: $white;
			border-radius: 100%;
			position: absolute;
			height: 20px;
			right: $gap;
			top: $gap;
			width: 20px;

			&:after, &:before {
				@include centerer(true, true);
				background-color: $primary;
				border-radius: 25px;
				content: '';
				display: block;
				height: 3px;
				width: 10px;
			}
			&:after {
				@extend %transition;
				transform:  translate(-50%, -50%) rotate(90deg);
			}

			&:active,
			&.active {
				background-color: $black;

				&:after, &:before {
					background-color: $white;
				}
				&:after {
					transform:  translate(-50%, -50%) rotate(0);
				}
			}
			&:hover {
				background-color: $black;

				&:after, &:before {
					background-color: $white;
				}
			}
		}

		&-panel {
			@extend %transition;
			display: none;
			margin: 0;
		}
		&-info {
			@extend %flex;
			@extend %transition;
			@include flexbox(100%);
			align-items: center;
			font-size: 1rem;
			font-weight: $bold;
			padding: $gap $gap * 2 $gap $gap;

			&-meta {
				@extend %font-content;
				color: $white;
				font-size: 1rem;
				padding: $gap / 4 0 $gap / 4 $gap * 2;
				position: relative;

				a {
					&:hover {
						text-decoration: underline;

						.icon-outer {
							background-color: $black;

							.icon {
								color: $white;
							}
						}
					}
				}

				.icon-outer {
					@extend %transition;
					@include centerer(false, true);
					background-color: $white;
					border-radius: 100%;
					height: 20px;
					left: 0;
					width: 20px;

					.icon {
						@include centerer(true, true);
						color: $primary;
						width: 12px;
					}
				}
				&.mail {
					.icon-outer {
						background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 -3.05176e-05H1C0.734867 0.000262449 0.4807 0.105728 0.293233 0.293203C0.105767 0.480678 0.0003 0.734836 0 0.999969V4.99997C0.000292967 5.2651 0.105758 5.51927 0.293233 5.70674C0.480708 5.8942 0.734867 5.99967 1 5.99997H7C7.26513 5.99968 7.5193 5.89421 7.70677 5.70674C7.89423 5.51926 7.9997 5.2651 8 4.99997V0.999969C7.99971 0.734836 7.89424 0.480669 7.70677 0.293203C7.51929 0.105736 7.26513 0.000269482 7 -3.05176e-05ZM1 0.499969H7C7.13255 0.5001 7.25967 0.552834 7.35342 0.646553C7.44713 0.740303 7.49987 0.867419 7.5 0.999969V1.12314L4 3.68989L0.5 1.12314V0.999969C0.50013 0.867419 0.552865 0.740303 0.646583 0.646553C0.740333 0.552836 0.86745 0.500103 1 0.499969ZM7 5.49997H1C0.86745 5.49984 0.740333 5.4471 0.646583 5.35339C0.552867 5.25964 0.500133 5.13252 0.5 4.99997V1.74347L3.85208 4.20164C3.89502 4.23305 3.94684 4.24998 4.00003 4.24998C4.05323 4.24998 4.10505 4.23305 4.14798 4.20164L7.50007 1.74347V4.99997C7.49994 5.13252 7.4472 5.25964 7.35348 5.35339C7.25973 5.4471 7.13255 5.49984 7 5.49997Z' fill='black'/%3E%3C/svg%3E%0A");
						background-repeat: no-repeat;
						background-position: center;
						background-size: 12px;

						.icon {
							display: none;
						}
					}
					a {
						&:hover {
							.icon-outer {
								background-image: url("data:image/svg+xml,%3Csvg width='8' height='6' viewBox='0 0 8 6' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M7 -3.05176e-05H1C0.734867 0.000262449 0.4807 0.105728 0.293233 0.293203C0.105767 0.480678 0.0003 0.734836 0 0.999969V4.99997C0.000292967 5.2651 0.105758 5.51927 0.293233 5.70674C0.480708 5.8942 0.734867 5.99967 1 5.99997H7C7.26513 5.99968 7.5193 5.89421 7.70677 5.70674C7.89423 5.51926 7.9997 5.2651 8 4.99997V0.999969C7.99971 0.734836 7.89424 0.480669 7.70677 0.293203C7.51929 0.105736 7.26513 0.000269482 7 -3.05176e-05ZM1 0.499969H7C7.13255 0.5001 7.25967 0.552834 7.35342 0.646553C7.44713 0.740303 7.49987 0.867419 7.5 0.999969V1.12314L4 3.68989L0.5 1.12314V0.999969C0.50013 0.867419 0.552865 0.740303 0.646583 0.646553C0.740333 0.552836 0.86745 0.500103 1 0.499969ZM7 5.49997H1C0.86745 5.49984 0.740333 5.4471 0.646583 5.35339C0.552867 5.25964 0.500133 5.13252 0.5 4.99997V1.74347L3.85208 4.20164C3.89502 4.23305 3.94684 4.24998 4.00003 4.24998C4.05323 4.24998 4.10505 4.23305 4.14798 4.20164L7.50007 1.74347V4.99997C7.49994 5.13252 7.4472 5.25964 7.35348 5.35339C7.25973 5.4471 7.13255 5.49984 7 5.49997Z' fill='white'/%3E%3C/svg%3E%0A");
							}
						}
					}
				}
