			&-image {
				@include flexbox(15%);

				img {
					aspect-ratio: 1;
					border-radius: $corner;
					height: 100%;
					object-fit: cover;
				}

				+ .sidebar-accordion-info-inner {
					@include flexbox(85%);
					padding-left: $gap;
				}
			}
			&-inner {
				@include flexbox(100%);
			}

			p {
				font-size: 1rem;
				font-weight: $normal;
				padding-top: $gap / 4;
			}
		}
		&-additional-info {
			padding: 0 $gap * 2 $gap $gap;
		}
	}
}
