/* ++++++++++++++++++++++++++++++++
+++ LAYOUT: INFOMATION OVERVIEW +++
++++++++++++++++++++++++++++++++ */

.info-overview {
	&-outer {
		&.inline {
			.info-overview {
				@include break(medium) {
					margin: 0 0 0 auto;
					width: 66.66%;
				}
				.info-overview-list {
					&-block {
						@include flexbox(100%);
					}
				}
			}
			&.secondary {
				.section-header {
					color: $black;
				}
				.info-overview-list-block  {
					background-color: $secondary;
					margin-top: $gap * 1.5;
					padding: $gap * 1.5;

					&.no-icon {
						background-position: top $gap * 1.5 left $gap * 1.5;
						background-size: 5%;

						@include break(tablet) {
							background-size: 10%;
						}

						@include break(large) {
							background-size: 76px;
						}
					}
				}
			}
		}
		&.secondary {
			.info-overview-list-block {
				&.no-icon {
					@include info('white');
					background-position: 0 .5rem;
					background-size: 5%;

					@include break(medium) {
						background-size: 10%;
					}
				}
			}
		}
	}

	&-list {
		@extend %flex;
		justify-content: space-between;

		&-block {
			@extend %flex;
			@include flexbox(100%);
			align-items: flex-start;
			margin: 0;
			padding: $gap / 1.5 0;

			@include break(medium) {
				@include flexbox(48%);
			}
			&.no-icon {
				@include info('black');
				background-position: 0 .5rem;
				background-size: 5%;

				@include break(medium) {
					background-size: 10%;
				}

				.info-overview-content {
					margin-right: 0;
				}
			}
		}
	}
	&-icon {
		@include flexbox(5%);

		@include break(medium) {
			@include flexbox(10%);
		}

		img {
			aspect-ratio: 1;
		}
	}
	&-content {
		@include flexbox(95%);
		padding-left: $gap;

		@include break(medium) {
			@include flexbox(90%);
		}
	}
	&-title {
		@extend %h4;
		padding-bottom: $gap / 2;
	}
}
