/* +++++++++++++++++++++++++++
+++ LAYOUT: RESPONSIVE NAV +++
+++++++++++++++++++++++++++ */

// Layout and styling of the navigation at mobile and tablet sizes

.menu-button-close {
	background-color: $white;
	border-radius: $corner;
	font-size: 2.5rem;
	font-weight: $bold;
	height: 3rem;
	position: absolute;
	right: $gap;
	top: $gap;
	width: 3rem;

	&:hover {
		text-decoration: none;
	}
}

.rnav {
	color: $white;
	margin-top: 0;
	padding-top: 0;

	&-outer {
		@extend %transition;
		background: $primary;
		height: 100vh;
		overflow: hidden;
		position: fixed;
		right: -92%;
		top: 0;
		width: 92%;
		z-index: 990;

		@include break(medium) {
			display: none !important;
		}

		&.show-nav {
			display: block;
			right: -1px;
		}
	}
	&-overlay {
		@extend %transition;
		-webkit-backdrop-filter: blur(4px);
		backdrop-filter: blur(4px);
		height: 100vh;
		left: 0;
		opacity: 0;
		pointer-events: none;
		position: absolute;
		top: 0;
		width: 100%;
		z-index: 989;

		@include break(medium) {
			display: none !important;
		}

		&.show-overlay {
			opacity: 1;
		}
	}

	&-search {
		padding: $gap 80px $gap $gap;

		.search {
			width: 100%;

			&-input {
				background: $white;
				border-color: transparent;
				color: $white;
				font-size: 1em;
				height: auto;
				padding: $gap / 1.5 $gap * 2.5 $gap / 1.5 $gap;
			}
			&-icon {
				height: 24px;
				right: $gap / 1.8;
				width: 24px;
			}
			&-button {
				border-left: $line solid rgba($white, 0.1);
				right: 0;
				width: 45px;
			}
		}
	}

	&-item {
		border-top: $line solid rgba($white, 0.1);
		position: relative;

		&-link {
			@extend %transition;
			display: block;
			padding: $gap $gap * 3 $gap $gap;
			text-align: left;

			&:hover {
				background: rgba($white, 0.1);
			}
		}
	}

	&-dropdown {
		@extend %transition;
		background: darken($tertiary, 5);
		height: 100vh;
		overflow: hidden;
		position: fixed;
		right: -82%;
		top: 0;
		width: 82%;
		z-index: 991;

		&.show-nav {
			right: 0;
		}

		.rnav-dropdown {
			background: darken($tertiary, 10);
			right: -72%;
			width: 72%;
			z-index: 993;

			&.show-nav {
				right: 0;
			}
		}
		.rnav-arrow {
			&.active {
				background-color: $white;
				right: 72%;
				z-index: 994;
			}
		}
	}

	&-secnav {
		background: $secondary;

		&-item {
			border-bottom: $line solid rgba($white, 0.1);
			display: block;

			&:last-child {
				border-bottom: 0;
			}

			.rnav-item-link {
				font-size: 0.9em;
				font-weight: $normal;

				&:hover {
					background: lighten($grey20, 5);
					border-color: $primary;
				}
			}
		}
	}

	&-socials {
		@extend %flex;
		flex-wrap: nowrap;

		.rnav-item {
			flex-grow: 1;

			&-link {
				@extend %flex;
				align-items: center;
				border: $line solid rgba($white, 0.1);
				justify-content: center;
				padding: $gap 0;
			}
		}
	}

	&-arrow {
		@extend %transition;
		background-color: $white;
		border-radius: $corner;
		box-shadow: none;
		cursor: pointer;
		height: 2.5rem;
		padding: $gap;
		position: absolute;
		right: $gap / 1.5;
		top: $gap / 2.2;
		width: 2.5rem;

		.icon {
			@include transition(transform 0.4s ease);
			@include centerer(true, true);
			display: block;
			fill: $black;
			width: $gap * 2;
		}

		&:hover {
			background-color: $secondary;
			box-shadow: none;
		}

		&.active {
			background-color: $white;
			border-radius: $corner 0 0 $corner;
			position: fixed;
			right: 82%;
			top: $gap;
			z-index: 992;
		}
	}

	&-button {
		border-top: $line solid rgba($white, 0.1);
		padding: $gap;
	}
}
