/* ++++++++++++++++++++++
+++ LAYOUT: ACCORDION +++
++++++++++++++++++++++ */

.accordion {
	background-color: $white;
	position: relative;

	.expand-all {
		background-color: transparent;
		font-size: 1.125rem;
		margin-bottom: $gap / 2;
		padding: 0;
		text-decoration: underline;
	}

	&-outer {
		&.two-col {
			&.inline {
				background-color: transparent;
				.section-header-inner {
					margin: 0 0 0 auto;
				}
				.accordion-col {
					@include break(medium) {
						margin: 0 0 0 auto;
						width: 66.66%;
					}
					.accordion {
						margin: 0;
					}
				}
			}
		}
		&.inline {
			.accordion {
				@include break(medium) {
					margin: 0 0 0 auto;
					width: 66.66%;
				}
			}
			&.secondary {
				.section-header {
					color: $black;
				}
				.accordion {
					background-color: $secondary;
					color: $white;
					padding: 0 $gap;

					&-button {
						color: $white;
					}
				}
			}
		}
		&.secondary {
			.accordion {
				padding: 0 $gap;

				&-panel {
					color: $black;
				}
			}
		}
	}

	&-col {
		@extend %flex;
		justify-content: space-between;

		.accordion {
			margin: 0;
			@include break(tablet) {
				@include flexbox(48%);
			}
		}
	}

	&-item {
		padding: $gap 0;

	    & + & {
	        border-top: $line solid $grey80;
	    }
	}

	&-trigger {
		* {
			pointer-events: none;
		}
	}

	&-button {
		background: none;
		border-radius: 0;
		color: $black;
		display: block;
		font-size: 1.25rem;
		padding: 0 $gap * 1.5 0 0;
		position: relative;
		text-align: left;
		width: 100%;

		.icon {
			display: inline-block;
			height: 1.05rem;
			pointer-events: none;
			position: absolute;
			right: 0;
			top: 0.3rem;
			transition: 0.235s transform ease-in-out;
			transform: rotate(90deg);
			width: 1.05rem;
		}

		&:hover,
		&:focus {
			background: none;
			box-shadow: none;
			outline: 0;
		}

		&:active,
		&.active {
			.icon {
				transform: rotate(-90deg);
				transition: 0.235s transform ease-in-out;
			}
		}
	}

	&-panel {
		display: none;
		padding-top: $gap;

		p {
			color: $black;
		}
	}
}
